import React, { Component } from 'react'
import DatePicker from 'react-datepicker'
import 'react-datepicker/dist/react-datepicker.css'

class CalendarAddEvent extends Component {
  constructor() {
    super()
    this.state = {
      startDate: new Date(),
      endDate: new Date(),
      title: '',
      allDay: false
    }
    this.handleChangeStart = this.handleChangeStart.bind(this)
    this.handleChangeEnd = this.handleChangeEnd.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
    this.handleChangeTitle = this.handleChangeTitle.bind(this)
    this.handleChangeAllDay = this.handleChangeAllDay.bind(this)
  }

  async handleSubmit() {
    const rawResponse = await fetch(
      'https://hpvtexas.wpengine.com/wp-json/wp/v2/event',
      {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + this.props.loginData.userToken
        },
        body: JSON.stringify({
          title: 'An event'
          // author: this.props.loginData.userData.username
          // acf: {
          //   all_day: false,
          //   start: this.state.startDate,
          //   end: this.state.endDate,
          //   approved: false
          // }
        })
      }
    )
    const res = await rawResponse.json()
    console.log(res)
  }

  handleChangeStart(date) {
    if (this.state.endDate < date) {
      this.setState({
        startDate: date,
        endDate: date
      })
    }
    this.setState({
      startDate: date
    })
  }
  handleChangeEnd(date) {
    this.setState({
      endDate: date
    })
  }

  handleChangeTitle(e) {
    this.setState({
      title: e.currentTarget.value
    })
  }
  handleChangeAllDay(e) {
    this.setState({
      allDay: e.currentTarget.value
    })
  }
  render() {
    console.log(this.props)
    return (
      <div className="calendar-add-event">
        <label>
          Event Name
          <input type="text" placeholder="Event Name" />
        </label>
        <label>
          All Day?
          <input type="checkbox" onChange={this.handleChangeAllDay} />
        </label>
        <label>
          Start Date
          <div className="icon-and-date-picker">
            <i className="far fa-calendar-alt" />
            <DatePicker
              selected={this.state.startDate}
              onChange={this.handleChangeStart}
              startDate={this.state.startDate}
              endDate={this.state.endDate}
              minDate={new Date()}
              name="startDate"
              dateFormat="MM/dd/yyyy h:mm aa"
              showTimeInput
              locale="en"
              selectsStart
              shouldCloseOnSelect={false}
            />
          </div>
        </label>
        <i className="fas fa-angle-right" />
        <label>
          End Date
          <div className="icon-and-date-picker">
            <i className="far fa-calendar-alt" />
            <DatePicker
              selected={this.state.endDate}
              onChange={this.handleChangeEnd}
              startDate={this.state.startDate}
              endDate={this.state.endDate}
              minDate={this.state.startDate}
              name="startDate"
              dateFormat="MM/dd/yyyy h:mm aa"
              showTimeInput
              locale="en"
              selectsEnd
              shouldCloseOnSelect={false}
            />
          </div>
        </label>
        <button onClick={this.handleSubmit}>Submit Event To Review</button>
      </div>
    )
  }
}

export default CalendarAddEvent
